				console.log('Confirmed tx: ', miningRes);
				createToast('Transaction confirmed!',
				{
					type: 'success',
					showIcon: true,
					position: 'bottom-right',
				});
			}

		} else {
			throw Error('Error creating tx', tx);
		}
		
		
	} catch (err) {
		let error = `${err}`;
		if (typeof(err) === 'object' &&
